import React from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	Checkbox,
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	useTheme,
	IconButton,
} from '@material-ui/core';

import { makeStyles } from '@material-ui/core/styles';
import { useQuery, gql } from '@apollo/client';
import { generatePath, Link } from 'react-router-dom';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import { TSellRequest } from '../../../types';
import { listSellRequests } from '../../../graphql/queries';
import { getComparator, stableSort } from '../../../utils/table';
import Loader from '../../../ui-component/Loader';
import Header from './ListHeader';
import LoadMore from '../components/LoadMore';
import config from '../../../config';
import { formatDate } from '../../../helpers/date';
import { useOrder, useSelectRow } from '../../../hooks';

const useStyles = makeStyles(() => ({
	table: {
		minWidth: '750px',
	},
}));

const SellRequestList = () => {
	const { loading, error, data, fetchMore } = useQuery(gql(listSellRequests), {
		variables: { limit: config.pagination.limit },
		fetchPolicy: 'cache-first',
		notifyOnNetworkStatusChange: true,
	});

	const rows: TSellRequest[] = _get(data, 'listSellRequests.items', []);

	const nextToken: string | null | undefined = _get(
		data,
		'listSellRequests.nextToken',
		undefined
	);
	const theme = useTheme();
	const classes = useStyles();
	const { order, orderBy, handleRequestSort } = useOrder('RequestId');
	const { selected, isSelected, handleSelect, handleSelectAll } =
		useSelectRow('RequestId');

	if (loading && !data) return <Loader />;
	if (error || _isEmpty(data)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Something went wrong!
			</Alert>
		);
	}

	const loadMore = () => {
		fetchMore({
			variables: { limit: config.pagination.limit, nextToken },
		});
	};

	const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
		handleSelectAll(event.target.checked, rows);
	};

	return (
		<>
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle' size='small'>
					<Header
						numSelected={selected.length}
						order={order}
						orderBy={orderBy}
						onSelectAllClick={handleSelectAllClick}
						onRequestSort={handleRequestSort}
						rowCount={rows.length}
					/>
					<TableBody>
						{stableSort<TSellRequest>(rows, getComparator(order, orderBy)).map(
							(row, index) => {
								if (typeof row === 'number') return null;
								const sellRequestEditLink = generatePath('/sell-request/:id', {
									id: row.RequestId,
								});
								const isItemSelected = isSelected(row.RequestId);
								const labelId = `enhanced-table-checkbox-${index}`;

								return (
									<TableRow
										hover
										onClick={() => handleSelect(row.RequestId)}
										role='checkbox'
										aria-checked={isItemSelected}
										tabIndex={-1}
										key={row.RequestId}
										selected={isItemSelected}
									>
										<TableCell sx={{ pl: 3 }} padding='checkbox'>
											<Checkbox
												color='primary'
												checked={isItemSelected}
												inputProps={{
													'aria-labelledby': labelId,
												}}
											/>
										</TableCell>
										<TableCell component='th' id={labelId} scope='row' padding='none'>
											{row.RequestId}
										</TableCell>
										<TableCell align='left'>{row.SellerInfo.Email}</TableCell>
										<TableCell align='left'>{formatDate(row.CreatedAt)}</TableCell>
										<TableCell align='left'>{formatDate(row.UpdatedAt)}</TableCell>
										<TableCell align='left' sx={{ pr: 3 }}>
											<Link to={sellRequestEditLink}>
												<IconButton color='primary'>
													<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
												</IconButton>
											</Link>
										</TableCell>
									</TableRow>
								);
							}
						)}
					</TableBody>
				</Table>
			</TableContainer>
			<LoadMore onLoadMore={loadMore} disabled={!nextToken} loading={loading} />
		</>
	);
};

export default SellRequestList;
